  }
  .news-container .item .left img{
    width: 100%;
    border-radius: 7px;
  }
  .news-container .item .right{
    padding:  32px;
    padding-top: 32px;
  }
  .news-container .item .right a{
    color: black;
    text-decoration: none !important;
  }
